// unstyled list

.unstyled {
  margin-left: 0;
  list-style: none;
  li {margin: 0.5rem 0;}
}

// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// checked alternate

ul.checked-alternate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;
  font-size: $fs-18;

  @include bp-m {margin: 0;}

  li {
    position: relative;
    //border-top: 4px solid white;
    padding: 0.6rem 2rem;
    padding-left: 2.5rem;
    
    &:first-child {border-top: 0;}
    &:nth-child(odd) {background: darken($clr-4,2%);}

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0.8rem; top: 0.65rem;
      @-moz-document url-prefix() {top: 0.55rem;}
      font-size: $fs-25;
      color: $clr-success;
      .bg-dark & {color: white;}
    }
  }
}

// checked list

ul.checked {
  margin-left: 0!important;
  list-style: none!important;
  font-size: $fs-18;

  &.success li:before {color: $clr-success;}

  li {
    position: relative;
    margin: 0.4rem 0;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check;
      position: absolute;
      left: 0; top: 0.2em;
      color: $clr-b1;
      //.bg-dark & {color: white;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration-line: underline;}
    }
  }

  div.product-overview & {
    text-align: left;
    font-size: $fs-20;
  }

  div.page-intro &,
  div.page-intro2 & {
    display: inline-block;
    //margin-top: $vmargin;
    text-align: left;
  }

  div.why-ms & {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    font-size: $fs-22;
  }
}

// cross list

ul.crossed {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin: 0.4rem 0;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-times;
      position: absolute;
      left: 0; top: 0.2em;
      color: $clr-error;

      div.pop-dark & {color: $clr-warning;}
    }
  }
}

// icons list

ul.icons {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin: 0.4rem 0;
    padding-left: 1.2em;

    &:before {
      @extend .icon-extension;
      position: absolute;
      left: 0; top: 0.2em;
      .bg-dark & {color: white;}
    }
  }
}

// asterisk lists

ul.asterisk,
ol.asterisk {
  margin-left: 0!important;
  list-style: none!important;
  font-size: $fs-18;

  div.strip.contact & {@extend .lead;}

  &.centered-list {
    display: inline-block;
    text-align: left;
  }

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.25em;
      color: $clr-b1;
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration-line: underline;}
    }
  }
}

// icon definition list

dl.asterisk,
dl.checked {
  margin-left: 0!important;
  list-style: none!important;
  font-size: $fs-18;

  dt,dd {padding-left: 1.4em;}

  dt {
    position: relative;
    margin-bottom: 0.25rem;
    font-size: $fs-18;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.25em;
      color: $clr-b1;
    }
  }
}

dl.checked {
  dt:before {@extend .icon-check;}
}

// styled list

ul.styled {
  border-radius: $border-radius;
  padding: calc($spacing*0.5) $spacing;
  background: $clr-4;

  @include bp-m {font-size: $fs-20;}

  .bg-dark &,
  .bg-b5 & {
    background: rgba(black,0.2);
    //li:before {color: white!important;}
  }

  //li {margin: 0.5rem 0!important;}
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.services & {color: $clr-b2;}
      body.resources & {color: $clr-b3;}
    }
  }

  &.plain a {
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration-line: underline;}
    }
  }
}

// buttons

ul.buttons {
  
  @include bp-s {
    display: flex;
    justify-content: center;
  }

  li {
    margin: 0.5rem 0;

    @include bp-s {
      flex: 0 1 auto;
      margin: 0.5rem 0.25rem;
    }
  }
}

// service list

ul.service-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: calc($vmargin*2);


  li:nth-of-type(odd) {
    padding-right: 3rem;
    text-align: right;
  }


  li:nth-of-type(event) {
    padding-left: 3rem;
    text-align: left;
  }

  li {
    padding: 0.4rem 0;
    width: 50%;

    a {
      text-decoration: none;
      font-weight: 700;
    }
  }
}

// hierarchy links

div.hierarchy {
  padding: 0.4rem 0;
  background: $clr-3;

  @include bp-s-less {@include hide-screen();}

  ol {
    margin: 0;
    font-size: $fs-16;

    @include bp-s {
      display: flex;
      align-items: baseline;
    }

    li {
      margin-right: 0.35rem;

      &:before {
        @extend .icon-extension, .icon-angle-left;
        flex: 0 1 auto;
        margin-right: 0.25rem;
        font-size: $fs-10;
      }
    
      a,
      > span {
        flex: 0 1 auto;
        text-decoration-line: none;
        color: #3B3D41;
      }
      
      a {
        @include bp-m {
          &:hover,
          &:focus {
            text-decoration-line: underline;
            color: $clr-b1;
          }
        }

        b {font-weight: 400;}
      }
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: $fs-20;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration-line: none;

      @include bp-m {
        &:hover,
        &:focus {text-decoration-line: underline;}
      }
    }
  }
}

// registration logos

ul.registration-logos {
  text-align: center;

  li {
    margin: 1.25rem 0;

    @include bp-m {margin-top: 0;}

    figure {
      margin: 0 auto;
      max-width: 275px;
    }
  }
}

// videos

ol.videos li.col {flex: 0 1 auto;}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file;
      position: absolute;
      left: 0; top: 1px;
      font-size: $fs-24;
    }
  }
}

// numbered discs

ol.numbered-discs {
  margin-left: 0!important;
  list-style: none!important;
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
    }
  }
}

// ordered blocks

ol.ordered-blocks {
  margin-top: calc($vmargin*2);
  counter-reset: numbered-blocks;
  text-align: center;

  @include bp-m {
    margin-top: 0;
    text-align: left;

    div.download-sample & {margin-bottom: 0;}
  }

  &.checks li:before {
    @extend .icon-extension,.icon-check;
  }

  li {
    position: relative;
    margin-bottom: 4px;
    padding: $spacing;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    @include bp-m {
      padding: calc($spacing/1.25) $spacing;
      padding-left: calc($spacing*1.75);
    }

    &:before {
      position: absolute;
      right: 50%; top: 0;
      margin: -1rem -1rem 0 0;
      border-radius: 50%;
      width: 2rem;
      height: 2rem;
      line-height: 2.2!important;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-18;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;
      box-shadow: 0 0 0 4px white;

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
      body.associates & {background: $clr-b4;}
      body.toolkit & {background: $clr-b6;}

      @include bp-m {right: 100%; top: 50%;}
    }

    > :last-child {margin-bottom: 0;}
  }
}

// product funnels

ol.product-funnels {
  text-align: center;

  &.consulting,
  &.toolkit,
  &.resources {
    div.text {
      text-align: left;

      //ul {font-size: $fs-18;}
    }
  }

  > li {

    a.content,
    div.content {
      display: block;
      background-color: $clr-4;
      transition: background-color 0.15s;
      text-decoration: none;

      div.bg-b5 &,
      &.bg-b5 {background-color: rgba($clr-2,0.05);}

      @include bp-m {
        display: flex;
        flex-direction: column;
        flex: 1;

        &:hover,
        &:focus {
          background-color: $clr-3;
          div.bg-b5 &, {background-color: rgba($clr-2,0.125);}
          &.bg-b5 {background-color: rgba($clr-2,0.125);}

          img {transform: scale(1.05);}
        }
      }

      span.resource,
      div.text {color: $clr-1;}

      figure {
        overflow: hidden;
        margin: 0;

        img {
          display: block;
          width: 100%;
          height: auto;
          transition: transform 0.15s;
        }
      }

      // Header

      div.header {
        padding: calc($spacing/1.25) calc($spacing/2);
        background-color: rgba(black,0.075);

        div.bg-b5 & {background-color: rgba(black,0.075);}

        h2,h3 {
          @extend .h4;
          margin: 0;

          div.toolkit-categories & {
            span.heading {
              display: block;
              span {display: block;}
            }

            span.category {margin-bottom: 0;}
          }

          span.resource {
            display: block;
            font-size: $fs-18;
            font-weight: 700;
          }

          span.assets {
            display: block;
            margin-top: 0.25rem;
            font-size: $fs-16;
            font-weight: 700;
            color: $clr-1;

            > span:not(:first-child):before {content: " + ";}
          }
        }
      }

      // Text 

      div.text {
        flex: 1;
        padding: $spacing;
        font-size: $fs-18;
        font-weight: 400;

        div.toolkit-categories & {
          text-align: left;
        }

        @include bp-m {flex: 1;}

        > :last-child {margin-bottom: 0;}

        ul.checked {font-size: $fs-16;}
      }

      // CTA

      div.cta {
        flex: 0 1 auto;
        padding: 0 $spacing $spacing;
        font-size: $fs-18;

        > :last-child {margin-bottom: 0;}
      }
    }
  }
}

// power stats

ul.power-stats {
  margin-bottom: 0;
  text-align: center;
  font-size: $fs-35;
  font-weight: 300;

  @include bp-m {padding: $spacing 0;}

  li {
    margin-top: $vmargin; margin-bottom: $vmargin;
    @include bp-m {margin-top: 0; margin-bottom: 0;}

    div.content {
      border-radius: $border-radius;
      padding: $spacing;
      background: rgba(black,0.2);

      strong {
        display: block;
        font-size: $fs-70;
        font-weight: 800;

        @include bp-m {font-size: $fs-60;}
        @include bp-l {font-size: $fs-70;}

        span {color: $clr-b1;}
      }

      @include bp-m {
        > span span {display: block;}
      }
    }
  }
}

// product summary

.product-summary {
  div.toolkit-packages & {
    @include bp-m {
      li:first-child div.content {
        border-top-left-radius: $border-radius;
        border-bottom-left-radius: $border-radius;
      }

      li:last-child div.content {
        border-top-right-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
      }

      li.col-5:first-child div.content {border-bottom-left-radius: 0;}
      li.col-5:nth-child(2) div.content {border-top-right-radius: $border-radius;}
      li.col-5:nth-child(3) div.content {border-bottom-left-radius: $border-radius;}
      li.col-5:last-child div.content {border-top-right-radius: 0;}
    }
  }

  div.toolkit-checkout & {
    @include bp-m {
      div.content {
        margin-left: auto; margin-right: auto;
        border-radius: $border-radius;
        width: $col-6;
      }
    }
  }

  div.content {
    overflow: hidden;
    display: block;

    @include bp-m {
      display: flex;
      flex-direction: column;
      flex: 1;
    }

    div.header {background-color: rgba(black,0.2);}

    div.text,
    span.frequency,
    span.info {color: white;}

    ul.checked a {
      font-weight: 700;
      color: #fffb8f;
    }
    
    // Header

    div.header {
      padding: $spacing;
      text-align: center;

      &:not[class*="bg-dark"] {background-color: $clr-3;}

      h3 {
        @extend .h4;
        margin: 0;

        div.toolkit-checkout & {@extend .h2;}

        div.toolkit-packages & {
          span,
          b {display: block;}
        }
      }

      p.description {
        margin: 0.7rem 0 0 0;
        font-size: $fs-17;
      }
    }

    // Included

    div.included {
      flex: 1;
      padding: calc($spacing/1.5);
      text-align: center;

      ul.icons {
        display: inline-block;
        margin: 0;
        text-align: left;
        line-height: 1.3;
        font-size: $fs-17;

        li {
          &.main-product {padding-left: 0;}

          a {
            text-decoration-line: none;

            @include bp-m {
              &:hover,
              &:focus {text-decoration-line: underline;}
            }
          }
        }
      }
    }

    // Text 

    div.text {
      flex: 1;
      padding: $spacing;
      font-size: $fs-18;

      @include bp-m {
        flex: 1;
        span.button {display: none;}
      }

      > :last-child {margin-bottom: 0;}

      ul {
        margin-bottom: 0;
        text-align: left;
        font-size: $fs-18;

        &.checked {
          line-height: 1.3;
          li:before {color: $clr-b1;}
        }
      }
    }

    // CTA + Prices

    div.cta {
      flex: 0 1 auto;
      padding: 0 $spacing $spacing;
      text-align: center;
      font-size: $fs-18;

      div.toolkit-checkout & {padding-bottom: calc($spacing*1.25);}

      > :last-child {margin: 0.7rem 0 0;}

      div.button {margin-top: 0;}
    }
  }
}

// product assets

ul.product-assets {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  border-radius: 0.25rem;
  padding: 0.2rem 0.5rem;
  background: rgba(white,0.075);
  
  div.product-overview & {background: $clr-4;}

  ol.product-funnels &,
  div.product-summary & {
    margin: 0.7rem 0 0 0;
    font-size: $fs-16;
    li {margin: 0 0.25rem;}
  }

  li {
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    margin: 0 0.5rem;
    
    &:before {margin-right: 0.2rem;}
  }
}

// information

.information {

  // from an article
  &.info-process {
    div.content {
      h3 {
        background: rgba(black,0.2);
        font-size: $fs-22;

        span {
          display: block;
          font-size: $fs-18;
          font-weight: 700;
        }
      }

      ol {
        margin: 0 0 0 1.25rem;
        padding: calc($spacing*0.75) calc($spacing/1.5);
        list-style: decimal;
        font-size: $fs-18;
      }
    }
  }

  &.specialities {
    div.content {
      div.text {
        font-size: $fs-18;

        ul {
          @extend .asterisk;
          li a {
            text-decoration-line: none;
            em {font-style: normal;}
            span.snippet {@include hide-screen();}

            @include bp-m {
              &:hover,
              &:focus {text-decoration-line: underline;}
            }
          }
        }
      }
    }
  }

  .content {
    flex: 1;
    &:not([class*="bg-dark"],[class*="bg-b5"]) {background: $clr-4;}

    h2,h3 {
      margin: 0;
      padding: calc($spacing*0.75) calc($spacing/1.5);
      text-align: center;
      color: $clr-n3;
      &:not([class*="bg-dark"],[class*="bg-b5"]) {background: $clr-3;}

      &[class*="icon-"]:before {
        display: block;
        margin-bottom: 0.25rem;
        font-size: $fs-35;
        color: $clr-b1;

        @include bp-m {font-size: $fs-40;}
      }

      @include bp-m {
        span {display: block;}
      }
    }

    body.landing &,
    body.services & {
      h3 {font-size: $fs-23;}

      div.text p {font-size: $fs-18; text-align: left;}
    }
  
    .bg-b5 & {
      background: rgba($clr-2,0.05);
      h3 {background: rgba($clr-2,0.05);}
    }

    .bg-dark & {
      background: rgba(black,0.2);
      h3 {background: rgba(black,0.5);}
    }

    .icon-para & {
      @include bp-m {
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }

    img {
      display: block;
      width: 100%;
      height: auto;
    }

    div.text {
      flex: 1;
      padding: $spacing;
      > :last-child {margin-bottom: 0;}
    }

    > p {
      margin: 0;
      padding: $spacing;

      @include bp-m {padding: $spacing*1.5;}

      em { // about page
        display: block;
        margin-bottom: calc($vmargin/2);
        font: italic $fs-30 Georgia, Serif;
      }

      &[class*="icon-"] {
        display: flex;
        align-items: flex-start;
        font-size: $fs-22;

        &:before {
          flex: 0 1 auto;
          margin-right: 0.3em;
          font-size: $fs-33;
        }
      }

      &.icon-check-circle:before {
        color: $clr-success;
        .bg-dark & {color: white;}
      }
    }
  }
}

// team

ul.team {
  li.col {
    div.content {
      padding: calc($spacing*1.25);
      background: $clr-4;

      @include bp-m {
        padding: calc($spacing*1.75);
      }

      > :last-child {margin: 0;}

      h3 {
        text-align: center;

        @include bp-m {
          display: flex;
          align-items: center;
          text-align: left;
        }

        span.team-photo {
          flex: 0 1 auto;
          margin-bottom: calc($vmargin/2);
          font-size: $fs-40;
          text-align: center;

          @include bp-m {
            margin-bottom: 0;
            font-size: $fs-30;
          }

          @include bp-l {
            margin-bottom: 0;
            font-size: $fs-50;
          }
        }

        span.credentials {
          flex: 1;

          @include bp-m {padding-left: $spacing;}

          a.name {
            font-size: $fs-22;
            font-weight: 800;
          
            &.single-social {
              display: flex;
              align-items: center;
              justify-content: center;
              text-decoration: none;
              transition: box-shadow 0.1s;

              &.icon-linkedin:before {
                order: 2;
                display: inline-block;
                margin-left: 0.5rem;
                border-radius: 0.2rem;
                width: 1.3rem;
                height: 1.3rem;
                line-height: 1.3rem;
                background: #0077B5;
                font-size: $fs-18;
                text-decoration: none;
                text-align: center;
                color: white;
              }

              @include bp-m {
                justify-content: start;

                &:hover,
                &:focus {
                  &:before {background: $clr-b1;}
                }
              }
            }
          }

          span.title {
            display: block;
            //margin-top: 0.25rem;
            font-weight: 400;
            font-size: $fs-17;
          }
        }
      }
        
      p {
        padding: 0;
        font-size: $fs-17;
      }
    }
  }
}

// process - clinical evaluation stuff

ol.process {
  counter-reset: process;

  > li {
    counter-increment: process;

    @include bp-m {
      display: flex;
      align-items: stretch;
      width: $col-6;
    }

    &:nth-child(4),
    &:nth-child(5) {
      > div.general-wrap div.content div.text h3 {background: darken($clr-4,5%);}
    }

    &:nth-child(6),
    &:nth-child(7) {
      > div.general-wrap div.content div.text h3 {background: darken($clr-4,10%);}
    }


    &:nth-child(8),
    &:nth-child(9) {
      > div.general-wrap div.content div.text h3 {background: darken($clr-4,15%);}
    }

    &:first-child {
      div.image {
        @include bp-m {order: 2;}
      }
    }

    &:last-child > div.general-wrap {margin-bottom: $vmargin;}

    &:first-child,
    &:last-child {
      display: block;
      width: $col-12;

      > div.general-wrap {

        div.content {
          @include bp-m {
            display: flex;
            align-items: stretch;
          }

          div.image {
            height: 200px;
            background: no-repeat 50%;
            background-size: cover;

            @include bp-s {height: 250px;}

            @include bp-m {
              width: $col-6;
              height: auto;
            }
          }
        
          div.text {
            @include bp-m {
              width: $col-6;
            }

            h3 {
              background: $clr-b2;
              color: white;
            }

            p {
              font-size: $fs-23;
              font-weight: 300;
              text-align: center;
            }
          }
        }
      }
    }

    > div.general-wrap {
      box-shadow: 0 0 30px 10px rgba(black,0.1);

      @include bp-m {margin-bottom: calc($vmargin*2);}

      div.content {
        text-align: center;

        div.text {
          padding: $spacing;
          padding-top: 0;

          @include bp-m {
            padding: calc($spacing*1.5);
            padding-top: 0;
          }

          >:last-child {margin-bottom: 0;}
        }

        h3 {
          @extend .h4;
          display: inline-block;
          border-radius: 0 0 0.3rem 0.3rem;
          padding: 0.25rem 1rem;
          background: $clr-4;
          font-weight: 300;

          &:before {
            content: counter(process) ". ";
          }
        }

        p {
          text-align: left;
          font-size: $fs-18;
        }
      }
    }
  }
}

// contact buttons

ul.contact-buttons {
  font-size: $fs-17;
  @include bp-xl {font-size: $fs-18;}

  li {
    display: flex;
    min-width: 0; // hack to make the email address wrap inside a flex child 

    a.content,
    a.copy-email {
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      padding: calc($spacing/1.5);
      min-width: 0; // hack to make the email address wrap inside a flex child 
      background: $clr-4;
      font-weight: 700;
      text-align: left;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.15s, color 0.15s;

      .bg-b5 & {background: rgba($clr-2,0.075);}

      div.cta &,
      div.bg-dark & {
        background: rgba(white,0.05);
        color: white;
      }

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;
          color: $clr-b1;
          div.bg-b5 & {background: rgba($clr-2,0.125);}
          div.bg-dark & {background: rgba(white,0.075);}
        }
      }

      &:before {
        margin: 0 0.5rem 0 0;
        font-size: $fs-25;
        color: $clr-b1;

        @include bp-s {font-size: $fs-27;}
      }

      div.cta-box &,
      ol.process & {
        @include bp-m {
          background: $clr-b0;
          color: white;

          &:before {color: white;}
        }

        @include bp-m {
          &:hover,
          &:focus {
            background: lighten($clr-b1, 5%)!important;
            box-shadow: 0 0 0 5px rgba(black,0.075)!important;
          }
        }
      }

      &[class*="linkedin"]:before {color: #0a66c2;}
      &[class*="youtube"]:before {color: #d70000;}
      &[class*="twitter"]:before {color: #1da1f2;}

      > span {
        @extend .text-wrap;
        min-width: 0; // hack to make the email address wrap inside a flex child 
      }

      &.email-address {
        span.address {
          @include hide-screen();
          @include bp-l {@include reset-hide-screen();}
        }

        span.text {
          @include bp-l {@include hide-screen();}
        }
      }

      &.phone > span {
        > span {
          @include hide-screen();
        }
      }
    }

    a.copy-email {
      flex: 0 0 auto;
      margin-left: 2px;
      text-align: left;
      //color: $clr-b1;

      &:before {
        margin: 0;
        font-size: $fs-20;
      }

      > span {@include hide-screen();}
    }
  }
}

// contact options

ul.contact-options {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.2rem; left: 0;
      font-size: $fs-24;

      footer.footer & {top: 0.05rem;}
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        @include bp-s-less {@include hide-screen();}
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration-line: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {text-decoration-line: underline;}
        }
      }
    }

    span.icon-angle-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

