// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-3;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li {
        a {
          padding-left: 2rem;
          background: $clr-4;
          font-weight: 400;

          &:before {content: none;}
          b {font-weight: 400;}
          span.snippet {@include hide-screen();}
        }

        &:last-child a {border-bottom: 0;}
      }
    }
  }
}

// faqs 

ol.faqs {
  margin: 0;
  text-align: left;
  //counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    //counter-increment: faq-item;

    &:last-child {margin-bottom: 0;}

    input {
      @include hide-input();

      + h3 {@extend .h4;}

      + h3, + h4 {
        margin: 0;
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: $clr-4;
          color: $clr-n0;
          transition: background-color 0.15s, color 0.15s;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;

          .bg-5 & {background: rgba($clr-n3,0.05);}

          &:before {
            @extend .icon-extension,.icon-plus-circle;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.5rem 1rem 0 0;
            font-size: 1em;
            color: $clr-b1;
          }

          @include bp-m {
            &:hover {
              background: $clr-3;
              color: $clr-b1;

              .bg-5 & {background: rgba($clr-n3,0.125);}
            }
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: white;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3, + h4 {

          label {
            background: $clr-n3!important; // prevent hover effect
            color: white;

            &:before {
              @extend .icon-minus-circle;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

