// articles
// * provides white grid border effect whilst giving the list a background colour 

ol.articles {
  position: relative;
  z-index: 1;

  @include bp-m {background: rgba($clr-4,0.5);}// *

  li.col {
    @include bp-m {box-shadow: 4px 0 0 0 white, 0 4px 0 0 white;}// *

    a {
      display: block;
      background: $clr-4;
      text-decoration: none;
      //color: $clr-1;
      transition: background-color 0.15s, color 0.15s;

      @include bp-m {
        display: flex;
        flex-direction: column;

        &:hover,
        &:focus {
          background: $clr-3;
          div.text h3 {color: $clr-b1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin-bottom: 0;

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;

        @include bp-m {
          display: flex;
          flex-direction: column;
          flex: 1;
        }

        h3 {
          @extend .h4;
          margin-bottom: calc($vmargin/2);
          //font-weight: 400;
          //color: $clr-b0;

          @include bp-m {flex: 0 1 auto;}
        }

        h3 + p {
          margin-bottom: 1rem;
          font-weight: 400;
          font-size: $fs-18;
          color: $clr-1;
          @include bp-m {flex: 1;}
        }

        div.meta {
          display: flex;
          align-items: center;
          font-size: $fs-15;
          font-weight: 400;
          color: $clr-1;

          @include bp-m {flex: 0 1 auto;}

          img.author {
            overflow: hidden;
            flex: 0 0 auto;
            vertical-align: middle;
            margin-right: 0.5rem;
            border-radius: 3rem;
            width: 2.25rem;
            height: 2.25rem;
            box-shadow: 0 0 0 3px $clr-4;
          }

          span {
            flex: 1;

            time,
            > span {
              display: block;
              margin-top: 0.1rem;
              line-height: 1.2;
            }

          }
        }
      }
    }
  }
}

// related articles

ul.related-articles {
  li {
    a {
      display: block;
      background: rgba(white,0.75);
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.2s, color 0.2s;

      @include bp-m {
        display: flex;
        align-items: center;

        &:hover,
        &:focus {
          background: $clr-3;
          div.text h3 {color: $clr-b1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin: 0;
        
        @include bp-m {width: 40%;}

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;

        @include bp-m {
          flex: 1;
          padding: calc($spacing/2) $spacing;
        }

        h3 {
          margin-bottom: calc($vmargin/2);
          font-size: $fs-22;
          font-weight: 400;
          color: $clr-b0;
        }

        p {
          margin-bottom: 0;
          $font-size: $fs-18;

          @include bp-m {position: absolute; left: -1000em;}
          @include bp-l {position: static;}
        }
      }
    }
  }
}

// services funnels

div.services-funnels {
  position: relative;
  color: white;

  @include bp-m {
    background: darken($clr-n3,0%);

    &:after {
      position: absolute;
      top: 0; bottom: 0; right: 0;
      z-index: 1;
      width: 70%;
      background: rgba(black,0.3);
      content: "";
    }
  }

  > ul {
    position: relative;
    z-index: 2;
    margin: 0;
    
    //body.mdr & {min-height: 31em;}
    //body.ivdr & {min-height: 33em;}
    body.heor & {min-height: 32em;}
    body.testing & {min-height: 37em;}

    > li {
      @include bp-m-less {
        margin-bottom: $vmargin;
        border-radius: $border-radius;
        background: $clr-n3;

        &:nth-of-type(even) {background: darken($clr-n3,5%);}
      }

      // input 
      input {
        position: fixed;
        top: 50%; left: -1000em;
        z-index: -1;
        visibility: hidden;

        @include bp-m-less {display: none;}

        &:checked + div.content {
          label {
            background: rgba(black,0.3);
            color: $clr-n4;
          }
          
          div.text {
            right: 0;
            opacity: 1;
            transition: opacity 0.2s 0.2s;
          }
        }
      }

      // content

      div.content {

        label {
          display: block;
          margin: 0;
          padding: $spacing;
          background: rgba(black,0.1);
          font-weight: 700;
          color: white;

          @include bp-m-less {display: none;}

          @include bp-m {
            position: relative;
            box-shadow: inset 0 1px 0 rgba(white,0.075);
            width: 30%;
            cursor: pointer;
            transition: background 0.2s;

            &:hover {
              background: rgba(black,0.2);
            }
          }
        }

        div.text {
          padding: $spacing ($spacing*1.5);
          text-align: center;

          @include bp-m {
            display: flex;
            flex-direction: column;
            position: absolute;
            top: 0; bottom: 0; right: 1000em;
            width: 70%;
            opacity: 0;
            transition: opacity 0.2s, right 0.2s 0.2s;
          }

          @include bp-l {padding: $spacing ($spacing*2);}

          h2 {
            @extend .h3;
            text-align: center;

            span {
              display: inline-block;
              border-bottom: 4px solid $clr-b1;
              padding-bottom: 0.5rem;
            }
          }

          p.lead {

          }

          ul.checked {
            display: inline-block;
            margin-left: auto!important;
            margin-right: auto;
            text-align: left;

            li:before {
              color: $clr-n4;
            }
          }

          p.cta {
            margin-bottom: 0;
          }

        }
      }
    }
  }
}
