div.table-wrap {
  margin: 0 (-$gutter) $vmargin;

  @include bp-s {
    overflow: hidden;
    margin: 0 0 $vmargin;
    border-radius: $border-radius;
  }

  div.wrap > h2 + p.lead + & {
    margin-top: $vmargin*1.5;
    @include bp-m {margin-top: $vmargin*2;}
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0 0;
  width: 100%;
  line-height: $lh-headings;

  @include bp-m {
    margin-bottom: calc($vmargin*1.5);
    font-size: $fs-16;
  }

  .hide-row {display: none;}
  .hide-text {text-indent: -1000em;}

  .hide-text-mobile {
    @include bp-m-less {
      display: inline-block;
      text-indent: -1000em;
      visibility: hidden;
    }
  }

  caption {
    caption-side: bottom;
    padding: 0.75rem 1rem;
    background: rgba($clr-2,0.1);
    text-align: left;
    @include bp-s {font-size: $fs-16;}
  }

  th,td {
    border: 1px solid rgba($clr-2,0.1);
    padding: 0.5rem 1rem;
    font-weight: 400;
    vertical-align: middle;
  }

  thead tr {background: rgba(white,0.5);}

  tbody {
    tr {
      background: rgba(white,0.5);

      &:nth-child(odd) {background: rgba(white,0.8);}
    }
  }

  &.simple {
    font-size: $fs-17;

    th {
      background: $clr-4;
      font-weight: 700;
      @include bp-m {white-space: nowrap;}
    }

    th,td {
      border: 2px solid $clr-3;
      @include bp-m-less {padding: 0.5rem;}
    }
  }

  &.features {
    margin-bottom: 0;
    text-align: center;
    font-size: $fs-14;

    @include bp-s {font-size: $fs-18;}

    th,td {
      border-width: 0 0 1px 1px;
      vertical-align: middle;
      &:first-child {border-left: 0;}
    }


    span.prices {
      margin-bottom: 0.5rem;

      span {display: block;}

      span.price {
        font-size: $fs-40;
        line-height: 1.1;
      }

      span.vat,
      span.info {
        display: block;
        font-size: $fs-16;
      }

      span {
        @include bp-m {display: inline-block;}
      }
    }


    thead th.included {
      padding: $spacing calc($spacing/2);
      font-weight: 400;

      @include bp-m {
        width: 30%;
      }
    }
    
    tbody {

      span[class*="icon-"] {
        display: inline-block;
        &:before {display: inline-block; font-size: $fs-25; color: #ccc;}
        span {@include hide-screen();}
      }

      span.icon-check-circle:before {color: $clr-success;}

      th {
        text-align: left;
        color: $clr-1;

        &.heading {
          @extend .h5;
          background: rgba($clr-2,0.1);
        }
      }
    }
        
  }

  &.comparison {
    margin-bottom: 0;
    text-align: center;
    font-size: $fs-14;

    @include bp-s {font-size: $fs-18;}

    th,td {
      border-width: 0 0 1px 1px;
      &:first-child {border-left: 0;}
    }

    thead th {
      vertical-align: middle;

      &.description {
        border-left-width: 0;
        width: 3rem;
      }

      &.type {
        padding: $spacing calc($spacing/2);
        font-weight: 400;

        @include bp-m {
          width: 30%;
        }
        
        h3 {
          margin-bottom: 0;
          font-size: $fs-18;
          color: $clr-1;
          
          @include bp-s {font-size: $fs-22;}

          span.emblem {
            display: block;
            margin: 0 auto;
            width: 2rem;
            background: url(../../assets/images/logos/emblem.svg) no-repeat 50%;
            background-size: 100% auto;

            @include bp-m {
              max-width: 200px;
              width: auto;
              background-image: url(../../assets/images/logos/mantra-systems.svg);
            }

            &:before {
              display: block;
              padding-top: 82%;
              content: "";

              @include bp-m {padding-top: 12%;}
            }
            
            span {@include hide-screen();}
          }
        }
      }
    }

    tbody {

      span[class*="icon-"] {
        &:before {font-size: $fs-30; color: $clr-error;}
        span {@include hide-screen();}
      }

      span.icon-check-circle:before {color: $clr-success;}

      th {
        text-align: left;
        color: $clr-1;

        &.feature {
          h4 {
            margin-bottom: 0;
            font-size: $fs-17;
            font-weight: 700;
          }

          p {
            margin: 0.25rem 0 0;
            font-size: $fs-14;
            @include bp-s-less {@include hide-screen();}
          }
        }

        &.feature-reveal {
          cursor: help;
          
          @include bp-m {
            &:hover {
              box-shadow: inset 0 0 0 3rem $clr-b1;
              color: white;

              span.longform {
              
                &:before {color: white;}

                span {
                  z-index: 1;
                  left: 0;
                }
              }
            }
          }

          span.longform {
            position: relative;
            display: inline-block;
            visibility: hidden;

            @include bp-m {visibility: visible;}

            &:before {
              @extend .icon-extension;
              @extend .icon-question-circle;
              font-size: $fs-18;
              color: $clr-b1;
            }

            span {
              position: absolute;
              bottom: 100%; left: -1000em;
              margin: 0 0 10px -10px;
              border-radius: 0.5rem;
              padding: 0.5rem 1rem;
              width: 250px;
              background: $clr-2;
              color: white;
              font-size: $fs-14;
              box-shadow: 0 2px 4px rgba(black,0.3);

              @include bp-s {font-size: $fs-16;}

              &:after {
                position: absolute;
                top: 100%; left: 0;
                margin-left: 14px;
                border: 5px solid transparent;
                border-top-color: $clr-2;
                content: "";
              }
            }
          }
        }
      }
    }
  }
}
