// background themes


// new gradients

.bg-1 { // dark blue to orange
  background-color: $clr-n2;
  background-image:
    url(../../assets/images/content/patterns/landscape.svg),
    linear-gradient(to bottom, $clr-n3 0%, $clr-n2 100%);
  background-size: 99px auto;
  background-position: 50%;
  color: white;
}

.bg-g2 {
  background-color: rgba($clr-n4,0.5);
  background-image:
    linear-gradient(to top, white 0%, transparent 50%),
    url(../../assets/images/content/patterns/landscape-dark.svg);
  background-size: 99px auto;
  background-position: 50%;
}

.bg-3 {
  background-color: $clr-n3;
  background-image: url(../../assets/images/content/patterns/landscape.svg);
  background-size: 99px auto;
  background-position: 50%;
  color: white;
}

.bg-4 {
  background-color: lighten($clr-n4,7%);
  background-image: url(../../assets/images/content/patterns/landscape-dark.svg);
  background-size: 99px auto;
  background-position: 50%;
}

.bg-5 {
  background-color: lighten($clr-4,0%);
  background-image: url(../../assets/images/content/patterns/landscape-dark.svg);
  background-size: 99px auto;
  background-position: 50%;
}


// strip

div.strip {
  position: relative;
  padding: calc($spacing*1.5) 0;
  @include bp-m {padding: calc($spacing*2) 0;}

  &.section {
    padding: calc($spacing*2) 0 calc($spacing*1);

    @include bp-m {
      padding: calc($spacing*3) 0 calc($spacing*2);
      &.latest p.lead span {display: block;}
    }

    //div.wrap > h2 {@extend .h1;}

    &[class*="bg-dark"] {
      box-shadow: inset 0 1px 0 0 rgba(white,0.1), inset 0 -1px 0 0 rgba(white,0.1);
      div.wrap > h2 {font-weight: 200;}
      p.lead a {color: white;}
    }
  }

  &.core {@include bp-m-less {padding: 0;}}
  &.alt {padding-top: 0;}

  // contact 

  &.contact {
    @include bp-m {text-align: left;}
  }

  // newsletter

  &.section.newsletter-linkedin {
    overflow: hidden;
    margin-bottom: $vmargin;
    border-radius: $border-radius;
    padding: calc($spacing*1.5) 0;
    p {margin-bottom: 0.75rem;}
    ul.buttons {margin-bottom: 0;}
  }

  // team

  &.team {
    h3 {@extend .h2;}
  }
}

// service facet

div.service-facet {
  background-image: linear-gradient(to top, white 0%, transparent 50%);
  box-shadow: inset 0 4px rgba(black,0.05);

  &.swap {
    div.col-8 {order: 2;}
  }

  &.one {background-color: rgba($clr-n2,0.05);}
  &.two {background-color: rgba($clr-n4,0.15);}
  &.three {background-color: rgba($clr-n2,0.05);}
  &.four {background-color: rgba($clr-n4,0.15);}

  div.feature {
    margin-bottom: $vmargin;
    border-radius: 5px;
    padding: $spacing;
    background: white;
    box-shadow: 0 4px 2px rgba(black,0.075);

    h4 {
      border-left: 4px solid $clr-b1;
      padding-left: 0.5rem;
      font-size: $fs-22;
    }

    p {
      font-size: $fs-16;
    }

    > :last-child {margin-bottom: 0;}
  }

  ul.checked {
    display: flex;
    flex-wrap: wrap;
    font-size: $fs-16;

    li {
      flex: 0 1 auto;
      margin: 0.25rem 0;
      width: 50%;

      &:nth-of-type(odd) {padding-right: 1rem;}
    }
  }
}

// landing intro

div.landing-intro {
  position: relative;
  z-index: 2;
  padding: calc($spacing*4) 0 calc($spacing*2);
  background: no-repeat 50%;
  background-size: cover;
  text-align: center;

  @include bp-m {
    padding: calc($spacing*7) 0 calc($spacing*3);
    text-align: left;
  }

  &:before {
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
    z-index: 1;
    background: rgba(black,0.75);
    content: "";

    @include bp-m {
      background: linear-gradient(to right, rgba(black,1) 0%, rgba(black,0) 100%);
    }
  }

  div.wrap {
    position: relative;
    z-index: 2;
  }

  h1 {
    font-weight: 800;

    > span {
      @include bp-m {display: block;}
    }
  }

  ul.checked {
    display: inline-block;
    text-align: left;
    font-size: $fs-22;
  }
}

