// guide navigation

input.guide-nav-toggle {
  @include hide-input();

  &:checked + nav.guide-nav {

    @include bp-m {left: 0;}

    label.cta {
      position: static;

      span.button {
        box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-m {content: "";}}
    }

    div.guide-links {
      right: 0; left: 0;

      @include bp-m {
        right: auto; left: auto;
        box-shadow: 0.25rem 0 1.5rem rgba(black,0.3);
      }
    }

    + article div.page-intro p.cta label span[class*="icon-"]:before,
    + article div.guide-content div.guide-nav-end ul.icons li.icon-list-ul:before {@extend .icon-times;}
  }
}

$navWidth: 28rem;

nav.guide-nav {
  position: fixed;
  right: 0; bottom: 0;
  padding-bottom: 1rem;
  z-index: 1000;

  @include bp-m {
    right: auto; top: 0; left: (-$navWidth);
    padding-bottom: 0; 
    width: $navWidth;
    display: flex;
    align-items: stretch;
    flex-direction: column;
    transition: left 0.25s;
  }

  label.cta {
    cursor: pointer;

    //@include bp-s-less {@include hide-label();}
      
    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0;
        font-size: $fs-25;
      }

      @include bp-m {
        position: absolute;
        bottom: 0; left: 100%;
        margin: 0 0 1rem 1rem;
        width: auto;
        height: 3rem;

        &:before {font-size: $fs-30;}

        @include bp-m {
          padding: 0 $spacing;
        }
      }

      span {
        @include bp-m-less {
          @include hide-screen();
        }

        @include bp-m {
          margin-left: 0.35rem;
          white-space: nowrap;
        }
      }
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.guide-links {
    //@extend .bg-1;
    position: fixed;
    left: -1000em; top: 0; bottom: 0;
    z-index: 1;
    text-align: center;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    @include bp-m {
      flex: 1;
      position: relative;
      left: auto; top: auto; bottom: auto;
      box-shadow: 0 0 0 rgba(black,0.3);
      transition: box-shadow 0.25s;
    }

    h2 {
      @extend .h4;
      margin: 0;
      padding: 0.75rem 1rem;
      background: rgba(black,0.3);
      color: white;
      font-weight: 300;

      @include bp-m {text-align: left;}
    }

    > ul {
      display: inline-block;
      margin-bottom: 0;
      padding: 0.75rem 0.5rem 2rem 1px;
      font-size: $fs-18;
      text-align: left;

      @include bp-m {display: block;}

      ul {
        margin-bottom: 0;

        li {
          margin-left: 1.3rem;
          border-left: 1px solid rgba(white,0.2);
        }
      }

      > li {

        > ul {
          padding-left: 0.5rem;
          > li {margin-left: 0.55rem;}
        }

        > a { 
          padding-left: 1rem;
          font-weight: 700;

          &:before {content: none;}
        }
      }

      a {
        display: flex;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.2rem 0.5rem;
        padding-left: 0;
        text-decoration: none;
        color: white;
        
        @include bp-m {
          &:hover,
          &:focus {background: rgba(white,0.075);}
        }

        &.current {
          background: rgba(black,0.3);
          box-shadow: -1px 0 0 $clr-b1, inset 4px 0 0 $clr-b1;
          &:before {color: $clr-b1;}
        }

        &:before {
          flex: 0 1 auto;
          color: rgba(white,0.2);
          content: "\02014\000A0";
        }

        span {flex: 1 1 auto;}
      }
    }
  }
}






