        display: block;
        position: relative;
        width: 280px;
      
        &:before {
          position: absolute;
          left: calc(0rem - $spacing); bottom: calc(0rem - ($spacing*1.5));
          padding-top: 69%;
          width: 100%;
          background: no-repeat 50% 100%;
          background-size: 100% auto;
          content: "";
        }
      }

      div.wrap h2 {flex: 1 1 auto;}
