    &.software-as-a-medical-device span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
    &.gspr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/gspr-matcher.png);}
