// associates

body.associates div.features-intro {
  @include bp-m-less {padding-top: 0;}
}

// sale content

div.sale-content {
  h3 {@extend .h2;}
}

// article pages

div.article-content {
  margin: 0 auto;
  max-width: $article-width;

  @include bp-m {padding-bottom: calc($spacing*2);}

  > p:nth-of-type(1):not([class]) {@include lead();}
  ul {@extend .disc;}
  ol {@extend .decimal;}

  figure {
    overflow: hidden;
    border-radius: $border-radius;
    margin: 0 auto $vmargin;
    max-width: $article-width;

    &.border {border: 6px solid $clr-4;}
  }
}



// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  &.spacer-up + p {@extend .spacer-up;}

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}
    }
  }
}

// announcement

div.announcement {
  padding: $spacing;
  background: $clr-warning;
  text-align: center;
  color: $clr-1;

  div.page-intro & {
    margin-bottom: $vmargin;
    border-radius: $border-radius;
  }

  div.home-intro & {
    margin-top: $vmargin;
    font-size: $fs-25;

    a {font-weight: 700;}
  }

  //div.wrap > :last-child {margin-bottom: 0;}

  span.text {
    @include bp-m {display: inline-block;}

    > span {
      display: block;
      margin: calc($vmargin/3) 0;

      @include bp-m {
        display: inline-block;
        //margin-bottom: 0;
      }
    }

    .button {
      margin: calc($vmargin/3) 0;
      @include bp-m {margin-left: calc($spacing/2);}
    }
  }
}

// modal

div.modal {
  display: block;
  position: fixed;
  top: 0; left: -100em; bottom: 0;
  z-index: 999;
  padding-top: ($gutter*2);
  background: black;
  text-align: center;

  @include bp-s {
    padding: ($gutter*2) $gutter $gutter;
    background: rgba(black, 0.8);
  }  

  @include bp-m {padding: ($gutter*2);}  

  &:target {
    left: 0; right: 0;

    @include bp-s {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  div.iframe-container {
    position: absolute;
    overflow: hidden;
    margin: 0 auto;
    padding: 0 $gutter;
    max-width: 820px;
    width: 100%;
    background: #f7f7f7;

    @include bp-s-less {top: 2.75rem; bottom: 0; left: 0; right: 0;}

    @include bp-s {
      position: relative;
      border-radius: $border-radius;
    }

    iframe {
      position: absolute;
      top: 0; bottom: 0; left: 0; right: 0;
      display: block;
      z-index: 2;
      width: 100%;
      height: 100%;

      @include bp-s {
        position: relative;
        margin-bottom: -35px; // to hide the Pipedrive link
        height: 735px;
      }
    }
  }

  div.content {
    position: absolute;
    padding: $gutter;
    background: white;

    @include bp-s-less {top: 2.75rem; left: 0; right: 0; bottom: 0;}

    @include bp-s {
      position: relative;
      margin: 0 auto;
      border-radius: $border-radius;
      max-width: 540px;
    }

    div.text {
      position: relative;
      z-index: 2;
      font-size: $fs-18;

      >:last-child {margin-bottom: 0;}
    }
  }

  p.close {
    margin-bottom: -$vmargin;
    font-size: $fs-17;

    a {
      text-decoration: none;

      span {
        display: inline-block;
        position: relative;
        top: -2rem;
        z-index: 3;
        border-radius: 2rem;
        padding: 0.3rem 0.6rem;
        background: rgba(white,0.15);
        color: white;

        @include bp-m {
          &:hover,
          &:focus {
            background: $clr-b1;
            color: white;
            cursor: pointer;
          }
        }
      }

      @include bp-s {
        &:before {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1;
          content: "";
          cursor: default;
        }
      }
    }
  }
}

// promotions

div.promotion {
  padding: calc($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {color: white;}
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-4;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;
  text-align: left!important;
  color: $clr-1;

  &:before {
    position: absolute;
    left: 0.8rem; top: 0.95em;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.info {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
    }
  }

  &.info {
    &:before {
      @extend .icon-extension,.icon-info-circle;
    }
  }
}


