// supporting info - on guide and article pages

div.supporting-info {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-bottom: $vmargin;
  border-radius: $border-radius;
  padding: 0.5rem 1rem;
  background: $clr-3;

