  div.content {
    padding: 0 1rem;

    h2 {
      @extend .h5;
      margin: 0 0 0.5rem;
    }

    ul.icons {
      font-size: $fs-16;
      @include bp-m {margin: 0;}

      li {
        a,
        label.anchor {
          text-decoration-line: none;

          @include bp-m {
            &:hover,
            &:focus {text-decoration-line: underline;}
          }
        }
      }
    }
  }
}

