    border-radius: $border-radius;
  }

  &.bordered {
    border: 6px solid $clr-4;
  }

  div.video-insert,
  p.video-caption {
    //margin-left: (-$gutter); margin-right: (-$gutter);

    @include bp-m {
      margin: 0 auto;
      max-width: 600px;

      div.page-intro & {max-width: none;}
      div.why-ms & {max-width: none;}

      article & {
        float: right;
        clear: both;
        position: relative;
        z-index: 1;
        margin-left: $spacing;
        width: 50%;
      }

      &.playing,
      &.playing + p.video-caption {
        float: none;
        margin-left: 0;
        width: auto;
        max-width: none;
      }
    }
  }

  div.video-insert {
    position: relative;

    &.playing {
      &:before {
        display: block;
        padding-top: 56.23%;
        content: "";
      }
    }

    // &.playing,
    // a.cta {
    //   background: black;
    // }

    a.cta {
      position: relative;
      display: block;
      transition: filter 0.2s;

      @include bp-m {
        &:hover,
        &:focus {
          filter: brightness(90%);

          &:before {
            background-size: 15%;
            opacity: 1;
          }
        }
      }

      &:before {
        position: relative;
        z-index: 2;
        display: block;
        padding-top: 56.23%;
        background: url(../../assets/images/content/other/play.png) no-repeat 50%;
        background-size: 10%;
        cursor: pointer;
        content: "";
        opacity: 0.75;
        transition: background-size 0.1s, opacity 0.1s;
      }

      img {
        position: absolute;
        right: 0; top: 0; left: 0; bottom: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
    }

    object,
    video,
    iframe {
      position: absolute;
      top: 0; left: 0;
      border: 0;
      width: 100%;
      height: 100%;
      background: transparent;
    }
  }

  p.video-caption {
    margin-bottom: $vmargin;
    border-radius: 0 0 0.5rem 0.5rem;
    padding: 0.5rem 0.7rem;
    background: $clr-4;
    font-size: $fs-15;
  }
}

// testimonials

div.testimonials {
  margin: $vmargin 0;

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        @include hide-input();

        &:checked {
          + div.content {
            overflow: visible;
            //visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        //visibility: hidden; - don't add this because it causes Google Lighthouse to complain about hidden labels
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing calc($spacing/1.7);
          max-width: 750px;
          min-height: 18rem;
          background: $clr-4;

          .bg-b5 & {background: rgba($clr-1,0.05);}

          @include bp-s {
            padding: $spacing;
            min-height: 22rem;
          }

          @include bp-m {padding: $spacing calc($spacing*1.5);}

          div.quote {
            font-size: $fs-18;
            @include bp-m {font-size: $fs-20;}

            &.small {
              @include bp-s-less {
                p:last-child {@include hide-screen();}
              }
            }

            &.medium {
              font-size: $fs-22;
              @include bp-m {font-size: $fs-25;}
            }
             
            &.large {
              font-size: $fs-25;
              font-weight: 300;

              @include bp-s {font-size: $fs-30;}

              @include bp-m {
                font-size: $fs-40;
                font-weight: 200;
              }
            }
              
            p {
              &:before {content: "\0201C";}
              &:after {content: "\0201D";}
            }
          }

          p.source {
            margin-bottom: 0;
            font-style: normal;
            font-weight: 700;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-label();}
        }
      }
    }
  }
}

// article categories

div.article-categories {
  display: block;
  position: relative;
  z-index: 2;
  border-bottom: 4px solid white;
  padding: 0.75rem;
  background: $clr-4;
  font-size: $fs-16;

  @include bp-l {display: flex;}

  h2 {
    @extend .h5;
    flex: 1 1 auto;
    margin: 0 0 0 0.25rem;
  }

  div.select-toggle {
    display: none;
    overflow: hidden;
    position: relative;
    border-radius: 0.25rem;
    background: $clr-3;

    @include bp-l {
      display: flex;
      justify-content: flex-end;
      flex: 0 1 auto;
    }
  }

  // open

  input#category-toggle {
    @include hide-input();

    &:checked {
      + label.category-toggle {
        @extend .icon-extension, .icon-angle-right;
        position: static;
        padding: 0.25rem 0.5rem;
        background: $clr-2;
        line-height: $lh-general;
        color: white;

        @include bp-m {
          &:hover {background: $clr-b1;}
        }

        span {@include hide-screen();}
    
        + ul {
          &:before {content: none;}

          li {
            position: static;
            flex: 1 1 auto;
            box-shadow: 1px 0 0 $clr-4;
          }
        }
      }
    }
  }

  // closed

  label.category-toggle {
    display: block;
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    z-index: 2;
    cursor: pointer;

    span {@include hide-screen();}

    @include bp-m {
      &:hover + ul:before {background: $clr-b1;}
    }
  }

  ul {
    position: relative;
    z-index: 1;
    display: flex;
    margin: 0;

    &:before {
      display: block;
      padding: 0.25rem 0.5rem;
      font-weight: 700;
      background: $clr-2;
      color: white;
      content: "Filter by";
    }

    li {
      @include hide-label();
      margin: 0;

      label {
        display: block;
        padding: 0.25rem 0.5rem;
        cursor: pointer;
        animation: preventPointerEvents 1s ease forwards;
        pointer-events: none;
        user-select: none;
        -webkit-user-select: none;

        @include bp-m {
          &:hover {background: white;}
        }
      }
    }
  }
}

input.category-input {@include hide-input();}

input#tag-all:checked ~ div.general-wrap div.article-categories li.tag-all,
input#tag-1:checked ~ div.general-wrap div.article-categories li.tag-1,
input#tag-2:checked ~ div.general-wrap div.article-categories li.tag-2,
input#tag-3:checked ~ div.general-wrap div.article-categories li.tag-3,
input#tag-4:checked ~ div.general-wrap div.article-categories li.tag-4,
input#tag-5:checked ~ div.general-wrap div.article-categories li.tag-5,
input#tag-6:checked ~ div.general-wrap div.article-categories li.tag-6,
input#tag-7:checked ~ div.general-wrap div.article-categories li.tag-7,
input#tag-8:checked ~ div.general-wrap div.article-categories li.tag-8,
input#tag-9:checked ~ div.general-wrap div.article-categories li.tag-9 {
  position: static;
  label {background: white;}
}

input.category-input ~ div.general-wrap ol.articles li {display: none;}

input#tag-all:checked ~ div.general-wrap ol.articles li,
input#tag-1:checked ~ div.general-wrap ol.articles li.tag-1,
input#tag-2:checked ~ div.general-wrap ol.articles li.tag-2,
input#tag-3:checked ~ div.general-wrap ol.articles li.tag-3,
input#tag-4:checked ~ div.general-wrap ol.articles li.tag-4,
input#tag-5:checked ~ div.general-wrap ol.articles li.tag-5,
input#tag-6:checked ~ div.general-wrap ol.articles li.tag-6,
input#tag-7:checked ~ div.general-wrap ol.articles li.tag-7,
input#tag-8:checked ~ div.general-wrap ol.articles li.tag-8,
input#tag-9:checked ~ div.general-wrap ol.articles li.tag-9 {display: flex;}

// article funnels

div.article-funnels.general-wrap {
  margin-top: $vmargin;

  > h2 {
    @extend .h5;
    position: relative;
    z-index: 2;
    margin: 0;
    border-bottom: 4px solid white;
    padding: calc($spacing/2) $spacing;
    background: $clr-4;
  }

  p.more {
    margin: 0;
    border-top: 4px solid white;
    padding: 0.75rem;
    background: $clr-3;
    text-align: center;
  }
}
