footer.footer {
  background: darken($clr-n3,5%);
  font-size: $fs-14;
  color: white;

  @include bp-m {text-align: left;}

  body.analytics & {display: none;}

  a:not(.button) {
    color: white;
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {
        text-decoration-line: underline;
        color: $clr-n4;
      }
    }
  }

  // logos

  ul.registration-logos {

    @include bp-s {
      display: flex;
      justify-content: center;
    }

    @include bp-m {
      margin-bottom: calc($vmargin/1.5);
      justify-content: right;
    }

    li {
      display: inline-block;
      margin: 0.5rem 0;

      @include bp-s {margin: 0.5rem;}
      @include bp-m {margin: 0.5rem 0 0 1rem;}

      figure {
        max-width: none;

        img {
          width: auto;
          max-width: 100%;
          max-height: 70px;
        }
      }
    }
  }

  // site

  div.site {
    padding: calc($spacing*2) 0;

    div.links {
      display: flex;
      flex-wrap: wrap;
      margin: 0 (-$gutter);
      text-align: center;

      @include bp-m {
        flex-wrap: nowrap;
        margin-bottom: 0;
        text-align: left;
      }

      > div.area {
        flex: 1 1 auto;
        width: 100%;

        @include bp-s {
          margin-bottom: $vmargin;
          width: 50%;

          &:nth-of-type(odd) {padding: 0 calc($gutter/2) 0 $gutter;}
          &:nth-of-type(even) {padding: 0 $gutter 0 calc($gutter/2);}
        }

        @include bp-m {
          padding: 0 calc($gutter/2);
          width: auto;

          &:first-of-type {padding-left: $gutter;}
          &:last-of-type {padding-right: $gutter;}
        }
      
        h3 {
          margin: 0;
          font-size: $fs-18;
          font-weight: 700;
        }

        ul {
          margin-bottom: $vmargin;
          @include bp-s {margin-bottom: 0;}

          li {
            margin: 0.7rem 0 0 0;
            line-height: 1.2;

            a {
              font-weight: 400;
              &:before {content: none;}
              b {font-weight: 400;}
              span.snippet {@include hide-screen();}
            }
          }
        }
      }
    }

    // company

    div.company {
      text-align: center;

      @include bp-m {
        &.col {text-align: right;}

        body.landing & {margin-top: 0;}

        > div.col:first-child {text-align: left;}
        > div.col:last-child {text-align: right;}
      }

      h2 {
        display: inline-block;
        margin: 0 0 0.25rem;
        width: 100%;
        max-width: 265px;
        background: url(../../assets/images/logos/inverted-mantra-systems.svg) no-repeat 50% 100%; 
        background-size: 100% auto;

        &:before {
          display: block;
          padding-top: 12%;
          content: "";
        }

        span {@include hide-screen();}
      }

      ul.contact-buttons {
        @include bp-m {margin-bottom: calc($vmargin/1.5);}
        //body.landing & {@include hide-screen();}

        li {
          display: inline-block;

          a {
            border-radius: 0.25rem;
            padding: 0.5rem;
            background: rgba(white,0.1);
            text-decoration: none;

            &:before {
              margin: 0;
              font-size: $fs-25;
              color: white;
            }

            @include bp-m {
              &:hover,
              &:focus {background-color: rgba(white,0.2);}
            }
            
            span {@include hide-screen();}
          }
        }
      }

      p.location {
        margin: 0;
        b {display: block;}
      }

      p.info {margin: 0;}
    }
  }
}
