header.banner {
  position: absolute;
  top: 0; left: 0; right: 0;

  body.analytics & {display: none;}
  
  div.wrap {

    a.content-jump {
      position: absolute;
      top: 0; left: -1000em;
      z-index: 1000;
      padding: $nav-link-padding 0.4rem;
      background: $clr-b1;
      text-align: center;
      color: white;
      text-decoration: none;
      outline: none;

      &:active,
      &:focus {left: 0; right: 0;}
    }
  }

  nav {
    position: relative;
    z-index: 100;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    // logo

    p.logo {
      flex: 1 1 auto;
      position: relative;
      z-index: 3;
      margin: 0 1rem 0 0;
      font-size: $fs-22;

      body.landing.services & {
        > span {
          margin: 0 auto;
          padding-top: 2rem;
          //max-width: 250px;
          //background-image: url(../../assets/images/logos/mantra-systems-strapline-white.svg);
          
          @include bp-m {
            margin: 0;
            padding-top: 4rem;
            max-width: 280px;
          }
        }
      }

      a,
      > span {
        display: block;
        padding: 1.6rem 0 0.75rem;
        max-width: 200px;
        background: url(../../assets/images/logos/mantra-systems.svg) no-repeat 50% 72%; 
        background-size: 100% auto;
        text-decoration: none;

        body.nav-alt & {background-image: url(../../assets/images/logos/inverted-mantra-systems.svg);}

        @include bp-s {max-width: 250px;}

        @include bp-m {
          padding-top: $nav-link-padding;
          max-width: 250px;

          &.current {
            box-shadow: inset 0 5px $clr-b1;
            body.nav-alt & {box-shadow: inset 0 5px white;}
          }
        }

        &:before {
          display: block;
          content: "\00A0";
        }

        > span {@include hide-screen();}
      }

      a {
        @include bp-m {
          &:hover,
          &:focus {
            box-shadow: inset 0 5px $clr-b1;
            body.nav-alt & {box-shadow: inset 0 5px $clr-b1;}
          }
        }
      }
    }

    // menu toggle

    p.menu-toggle {
      position: fixed;
      top: 0; right: 0;
      z-index: 3;
      padding: 1rem 1rem 0 0;
      line-height: 1;
      cursor: pointer;

      @include bp-l {@include hide-screen();}

      label.button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3rem;
        padding: 0;
        width: 2.5rem;
        height: 2.5rem;
        font-size: $fs-25;

        @include bp-m {
          width: 3rem;
          height: 3rem;
          font-size: $fs-30;
        }

        &:before {
          @extend .icon-extension,.icon-bars;
        }

        span {@include hide-screen();}
      }
    }

    // links

    div.links {
      position: absolute;
      left: -1000em;
      text-align: left;
      font-size: $fs-22;

      @include bp-l {
        position: static;
        display: flex;
        align-items: center;
        flex: 0 1 auto;
        width: auto;
      }

      a {text-decoration: none;}

      ul.site {
        margin: 0;

        @include bp-m {
          display: flex;
          align-items: center;
          //position: relative;

          // &.main li a.text:before {
          //   // white arrow
          //   position: absolute;
          //   bottom: 0; left: 50%;
          //   z-index: 1;
          //   margin: 0 0 -20px -10px;
          //   border: 10px solid transparent;
          //   border-bottom-color: white;
          //   content: "";
          //   opacity: 0;
          // }

          &.associates li.return a.text:before {
            margin-right: 0.25rem;
            font-size: $fs-16;
          }
        }

        > li {

          @include bp-l {
            flex: 1 0 auto;

            &.current {
              box-shadow: inset 0 5px $clr-b1;

              body.nav-alt & {
                box-shadow: inset 0 5px white;
                span.title.new:after {color: white;}
              }

              //a {color: $clr-b0;}
            }

            &.contact {
              padding: 1.2rem 0 0 1.1rem;

              a.button {
                //background: none;
                font-size: inherit;
                font-weight: 700;

                body.nav-alt & {
                  color: white;
                  box-shadow: inset 0 0 0 2px white;
                }
              }
            }

            // if mouse hover sub menu is to appear
            &.services,
            &.resources,
            &.company {
              @media (hover: hover) and (pointer: fine) { // excludes touch screens
                animation: preventPointerEvents 1s ease forwards;
                pointer-events: none;

                &:hover {
                  > a.text {

                    background: white;
                    color: $clr-b1;
                    box-shadow: inset 0 5px $clr-b1;
                    transition: background 0.2s 0s;

                    // &:before {
                    //   margin-bottom: 0;
                    //   opacity: 1;
                    //   transition: opacity 0.2s 0.25s, margin-bottom 0.1s 0.25s;
                    // }
                  }

                  div.sub {
                    top: 100%;
                    opacity: 1;
                    animation: popAppear 0.25s ease-out forwards;
                  }
                }
              }
            }

            &.services div.sub.link-list:before {background-image: url(../../assets/images/content/services/general/medical-device-regulatory-consulting-services.jpg);}
            &.resources div.sub.link-list:before {background-image: url(../../assets/images/content/services/general/notepad-planning.jpg);}
            &.company div.sub.link-list:before {background-image: url(../../assets/images/content/services/general/happy-women.jpg);}
          }

          > a.text {
            position: relative;
            display: block;
            padding: 0.5rem 0.75rem;
            color: white;

            span.snippet {@include hide-screen();}

            @include bp-m {
              flex: 1 0 auto;
              border-radius: 0;
              padding: $nav-link-padding 1rem 0.75rem;
              background: none;
              color: $clr-n3;
              font-weight: 700;

              &:hover,
              &:focus {
                color: $clr-b1;
                box-shadow: inset 0 5px $clr-b1;
              }

              body.nav-alt & {
                color: white;         
                &:hover,
                &:focus {color: $clr-b1;}
              }
            }
          }
        }
      }
          
      div.sub {
        @include bp-l {
          position: absolute;
          top: -100em; right: 0; left: 0;
          display: flex;
          align-items: center;
          border-radius: 0.4rem;
          padding: $spacing;
          background: white;
          font-size: $fs-18;
          box-shadow: 0 3px 3px rgba(black,0.2);
          opacity: 0;
        }

        // link list

        &.link-list {

          &:before {
            display: block;
            margin: 0.5rem;
            margin-right: 4%;
            border-radius: $border-radius;
            background: $clr-n4 no-repeat 50%;
            background-size: cover;
            width: 31%;
            min-height: 8rem;
            content: "";
          }

          ul {
            display: flex;
            flex-wrap: wrap;
            margin: 0;
            width: 65%;

            li {
              flex: 1 1 auto;
              width: 50%;

              &.sub-mdr {order: 1; a:before {content: "M";}}
              &.sub-ivdr {order: 3; a:before {content: "I";}}
              &.sub-heor {order: 2; a:before {content: "H";}}
              &.sub-testing {order: 4; a:before {content: "P";}}

              &.sub-about {order: 2;}
              &.sub-contact {order: 4;}
              &.sub-faqs {order: 3;}
              &.sub-jobs {order: 5;}
              &.sub-newsletter {order: 1;}
              &.sub-linkedin {order: 6;}

              &.sub-mdr-guide {order: 1;}
              &.sub-enablechat {order: 3;}
              &.sub-guides {order: 2;}
              &.sub-courses {order: 4;}
              &.sub-templates {order: 6;}
              &.sub-articles {order: 5;}

              a {
                display: flex;
                align-items: center;
                border-radius: $border-radius;
                padding: 0.5rem;
                transition: background-color 0.1s;

                &:before {
                  flex: 0 0 auto;
                  display: block;
                  margin-right: 0.5rem;
                  border-radius: 5px;
                  width: 2rem;
                  height: 2rem;
                  line-height: 2rem;
                  background: $clr-n3;
                  text-align: center;
                  font-size: $fs-25;
                  font-weight: 400;
                  color: white;
                }

                > span {
                  flex: 1 1 auto;
                  line-height: 1.2;

                  b {
                    display: block;
                    font-weight: 800;
                    font-size: $fs-15;
                    text-transform: uppercase;
                  }

                  span.snippet {
                    display: block;
                    font-size: $fs-14;
                    font-weight: 400;
                    color: $clr-1;
                  }
                }

                @include bp-m {
                  &:hover,
                  &:focus {background: lighten($clr-n4,20%);}
                }

                &.current {
                  background: lighten($clr-n4,15%);
                  b {color: $clr-b1;}
                }
              }
            }
          }
        }
      }
    }
  }

// --------------------------------------
// -------------------------------------- menu open
// --------------------------------------

  input#menu-toggle {
    @include hide-input();

    &:checked + nav {
      display: block;
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 2000;
      overflow-y: auto;
      padding-bottom: 3rem;
      background: $clr-n3;
      -webkit-overflow-scrolling: touch;

      // logo

      p.logo {
        position: absolute;
        top: 1rem; left: 1rem;
        z-index: 3;
        margin: 0;
        padding: 0;

        a {
          @extend .button;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          z-index: 2;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          background: white;
          font-size: $fs-25;
          color: $clr-1;

          @include bp-m {
            width: 3rem;
            height: 3rem;
            font-size: $fs-30;

            &:hover,
            &:focus {
              background: $clr-b1;
              color: white;
            }
          }

          &:before {
            @extend .icon-extension, .icon-home;
            padding: 0;
          }

          > span {@include hide-screen();}
        }
      }

      // menu toggle

      p.menu-toggle label.button {
        &:before {@extend .icon-times;}
      }

      // links

      div.links {
        overflow: hidden;
        position: relative;
        left: auto;
        z-index: 2;
        margin: 5rem 1rem 2rem;
        border-radius: $border-radius;
        font-size: $fs-17;

        @include bp-m {
          align-items: baseline;
          padding: 0;
          font-size: $fs-18;
        }

        @include bp-l {font-size: $fs-20;}

        ul.site {
          @include bp-m {
            align-items: stretch;
            flex: 1;
          }

          &.associates {
            > li {

              &.return {
                @include bp-m {display: none;}

                a:before {
                  @include bp-m-less {display: none;}
                }
              }

              > a {border-bottom: 0;}
            }
          }
          
          > li {
            margin-bottom: $vmargin;
            background: rgba(black,0.3);

            @include bp-m-less {
              overflow: hidden;
              border-radius: $border-radius;
            }

            &:hover > a {box-shadow: none;}

            &.current {
              box-shadow: none;
              > a {
                border-bottom-color: $clr-b1;
                background: rgba(black,0.4);
                color: $clr-n4;
              }
            }

            @include bp-m {
              flex: 1 1 auto;
              margin: 0 4px 0 0;
              width: 25%;
              background: rgba(black,0.2);
            }

            &.company {margin-right: 0;}
            &.contact {display: none;} 

            > a {
              display: flex;
              flex-direction: column;
              border-bottom: 5px solid $clr-n4;
              padding: 0.6rem 1rem;
              background: rgba(black,0.1);
              font-size: $fs-25;
              color: white;
              transition: background-color 0.1s;

              @include bp-m {
                &:hover,
                &:focus {
                  background: rgba(white,0.05);
                  box-shadow: none;
                }
              }

              span.snippet {
                @include reset-hide-screen();
                order: 2;
                font-size: $fs-16;
                font-weight: 400;
                color: $clr-n6;
              }
            }
          }
        }

        div.sub {
          position: static;
          display: block;
          margin: 0;
          border-radius: 0;
          padding: 0;
          width: auto;
          background: none;
          box-shadow: none;
          opacity: 1;
          animation: none;

          &:before {content: none;}

          ul {
            display: block;
            width: auto;

            li {
              width: auto;

              &.contact {display: block;}

              a {
                display: block;
                border-radius: 0;
                padding: 0.6rem 1rem;
                color: white;
                box-shadow: 0 1px rgba(white,0.075);

                &:before {content: none;}

                @include bp-m {
                  &:hover,
                  &:focus {background: rgba(white,0.05);}
                }

                &.current {
                  background: rgba(black,0.4);
                  box-shadow: inset 5px 0 $clr-b1;

                  b {color: $clr-n4;}
                }

                span.snippet {
                  margin-top: 0.25rem;
                  color: $clr-n6;

                  b {
                    color: lighten($clr-b6,10%);
                    font-weight: 400;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
