@font-face {
  font-family: 'icomoon';
  src: url('../../assets/fonts/icons/icomoon.eot?136d9f');
  src: url('../../assets/fonts/icons/icomoon.eot?136d9f#iefix') format('embedded-opentype'),
    url('../../assets/fonts/icons/icomoon.ttf?136d9f') format('truetype'),
    url('../../assets/fonts/icons/icomoon.woff?136d9f') format('woff'),
    url('../../assets/fonts/icons/icomoon.svg?136d9f#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

[class*="icon-"]:before,
.icon-extension {
  display: inline-block;
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

