// page content width

.wrap {
  margin: 0 auto;
  padding: 0 $gutter;
  max-width: $site-width;

  body.analytics & {max-width: none;}

  &.packages {max-width: 80rem;}

  @include bp-m {
    padding-left: ($gutter*2);
    padding-right: ($gutter*2);
  }  

  @include bp-l {
    &.narrow {padding: 0 12.5%;}
  }  
}

.no-pad {
  padding-top: 0;
  padding-bottom: 0;
}

// grid

.row {

  @include bp-m {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-left: -$gutter;
    margin-right: -$gutter;

    &.lone > .col {flex: 0 1 auto;}
    &.centered {justify-content: center;}
    &.middled {align-items: center;}
    &.swap > :first-child {order: 2;}
  }
  
  > .col {
    @include bp-m {
      flex: 1 1 auto;
      padding: 0 $gutter;
    }
  }

  &.separate {
    @include bp-m {
      > .col {
        display: flex;
        flex: 0 1 auto;
        align-items: stretch;
        padding: 0 ($gutter);

        &:first-child {padding-left: $gutter;}
        &:last-child {padding-right: $gutter;}
      }
    }

    > .col {margin-top: 4px;}
  }

  &.tile {
    margin-top: -4px;

    @include bp-m {
      margin-left: -4px;
      margin-right: 0;

      > .col {
        display: flex;
        flex: 0 1 auto;
        align-items: stretch;
        padding: 0 0 0 4px;
      }
    }

    > .col {margin-top: 4px;}
  }

  &.no-gutter {
    margin-left: 0; margin-right: 0;
    > .col {padding: 0;}
  }
}

@include bp-m {
  .col-1 {width: $col-1;}
  .col-2 {width: $col-2;}
  .col-3 {width: $col-3;}
  .col-4 {width: $col-4;}
  .col-5 {width: $col-5;}
  .col-6 {width: $col-6;}
  .col-7 {width: $col-7;}
  .col-8 {width: $col-8;}
  .col-9 {width: $col-9;}
  .col-10 {width: $col-10;}
  .col-11 {width: $col-11;}
  .col-12 {width: $col-12;}
}