// brand colours

$clr-cta: #317AE2; // new blue for CTAs

$clr-b0: #d04402; // orange for links
$clr-b1: #E74B00; // orange for brand
$clr-b2: #084ca2; // blue 
$clr-b3: #49239A; // purple
$clr-b4: #721f70; // aubergine
$clr-b5: #F8F9FF; // grey
$clr-b6: #1C690C; // green
$clr-b7: #ffeeb5; // yellow

// complimentary colours

$clr-1:  #2C2E32; // text grey
$clr-2:  #3B3D41; // dark grey
$clr-3:  #DEECF2; // light grey
$clr-4:  #F0F7FA; // lighter grey


$clr-n0: #0085C5; // blue cta
$clr-b1: #e74b00; // orange for brand
$clr-n2: #752600; // dark orange
$clr-n3: #004f75; // dark blue
$clr-n4: #8ad9ff; // light blue
$clr-n5: #ffddcc;
$clr-n6: #ffeee5;


// secondary colours

$clr-fields: #CDD2DD; // form field borders
$clr-placeholder: #777777; // form placeholder
$clr-error: #A90000; // error red
$clr-warning: #ecd833; // warning yellow
$clr-success: #50A200; // success green

// font family

$ff-1: "Muli", "Muli Italic", "Open Sans", Sans-Serif;

// font sizes - variable number is pixels

$fs-base: 125%; // 20px if user browser settings remain unchanged

$fs-100: 5rem;
$fs-90: 4.5rem;
$fs-80: 4rem;
$fs-75: 3.75rem;
$fs-70: 3.5rem;
$fs-65: 3.25rem;
$fs-60: 3rem;
$fs-55: 2.75rem;
$fs-50: 2.5rem;
$fs-46: 2.3rem;
$fs-45: 2.25rem;
$fs-42: 2.1rem;
$fs-40: 2rem;
$fs-39: 1.9rem;
$fs-35: 1.75rem;
$fs-34: 1.7rem;
$fs-33: 1.65rem;
$fs-32: 1.6rem;
$fs-30: 1.5rem;
$fs-28: 1.4rem;
$fs-27: 1.35rem;
$fs-25: 1.25rem;
$fs-24: 1.2rem;
$fs-23: 1.15rem;
$fs-22: 1.1rem;
$fs-20: 1rem;
$fs-18: 0.9rem;
$fs-17: 0.85rem;
$fs-16: 0.8rem;
$fs-15: 0.75rem;
$fs-14: 0.7rem;
$fs-12: 0.6rem;
$fs-10: 0.5rem;

// line heights

$lh-general: 1.45;
$lh-headings: 1.3;

// layout settings

$site-width: 62rem; // 1140px max
$gutter: 1.25rem; // universal gutters
$spacing: 1.25rem; // universal spacing used throughout and multiplied if required
$vmargin: 1.5rem; // vertical margin applied to <p>, <ul>, <h2>, etc
$nav-link-padding: 1.95rem;
$border-radius: 0.5rem;
$article-width: 800px;

// form field sizes

$ff-s: 200px;
$ff-m: 400px;
$ff-l: 600px;

// column widths

$col-1: 8.333%;
$col-2: 16.666%;
$col-3: 25%;
$col-4: 33.333%;
$col-5: 41.666%;
$col-6: 50%;
$col-7: 58.333%;
$col-8: 66.666%;
$col-9: 75%;
$col-10: 83.333%;
$col-11: 91.666%;
$col-12: 100%;

// media query breakpoints

$s: 480px;
$m: 820px;
$l: 1120px;
$xl: 1320px;
