// breakpoints (min-width == wider than : max-width == narrower than)

// occassional use for styles only less than 's'
@mixin bp-s-less {
  @media (max-width: #{$s - 0.01}) {
    @content;
  }
}

// styles greater than 's'
@mixin bp-s {
  @media (min-width: #{$s}) {
    @content;
  }
}

// occassional use for styles only less than 'm'
@mixin bp-m-less {
  @media (max-width: #{$m - 0.01}) {
    @content;
  }
}

// styles greater than 'm'
@mixin bp-m {
  @media (min-width: #{$m}) {
    @content;
  }
}

// occassional use for styles only less than 'l'
@mixin bp-l-less {
  @media (max-width: #{$l - 0.01}) {
    @content;
  }
}

// styles greater than 'l'
@mixin bp-l {
  @media (min-width: #{$l}) {
    @content;
  }
}

// styles greater than 'xl'
@mixin bp-xl {
  @media (min-width: #{$xl}) {
    @content;
  }
}

// occassional use for styles between 's' and 'm'
@mixin bp-s2m {
  @media (min-width: #{$s}) and (max-width: #{$m - 0.01}) {
    @content;
  }
}

// occassional use for styles between 'm' and 'l'
@mixin bp-m2l {
  @media (min-width: #{$m}) and (max-width: #{$l - 0.01}) {
    @content;
  }
}

// occassional use for styles between 'm' and 'l'
@mixin bp-l2xl {
  @media (min-width: #{$l}) and (max-width: #{$xl - 0.01}) {
    @content;
  }
}

// prevents sub menu hover open on page reload
@keyframes preventPointerEvents {
  0%, 99% {pointer-events: none;}
  100% {pointer-events: all;}
}

// sub menu animation effect
@keyframes popAppear {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  35% {
    opacity: 1;
    transform: scale(1.025);
  }
  100%{
    opacity: 1;
    transform: scale(1);
  }
}

// styles

@mixin clearfix() {
  display: table;
  clear: both;
  content: "";
}

@mixin hide-screen() {
  position: absolute;
  overflow: hidden;
  clip: rect(0,0,0,0);
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
}

@mixin reset-hide-screen() {
  position: static;
  overflow: visible;
  clip: initial;
  margin: initial;
  border: initial;
  padding: initial;
  width: initial;
  height: initial;
}

@mixin lead() {
  font-weight: 300;
  font-size: $fs-22;
  @include bp-s {font-size: $fs-24;}
  @include bp-m {font-size: $fs-25;}
  @include bp-l {
    div.page-intro & {font-size: $fs-30;}
  }
}

@mixin hide-input() {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
}

@mixin hide-label() {
  position: fixed;
  top: 50%; left: -1000em;
}